<template>
  <div>
    <div class="btn-reset-ctn">
      <b-button class="filter-button btn-reset" @click="resetFilters" variant="outline-*">
        <img :src="getIcon('resetBtn.svg')" alt>
        <p class="filter-name">Reset filters</p>
      </b-button>
    </div>
    <div v-for="(filterGroup, key) in store.filtersList" :key="key" :title="key" active>
      <b-button v-b-toggle="key" class="filter-button" variant="outline-*">
        <img :src="getIcon(filterGroup.icon)" alt>
        <img class="filter-arrow" src="@/assets/filterArrow.png" alt>
        <p class="filter-name">{{ key }}</p>
      </b-button>
      <b-collapse :id="key">
        <div class="section-filters">
          <div v-for="filter in filterGroup.datas" :key="filter.filterName" class="filter-item">
            <p v-if="filter.type !== 'checkbox'">{{ filter.filterName }}</p>
            <b-form-checkbox
              v-if="filter.type === 'checkbox'"
              v-model="filter.checked"
              @change="
                addFilter(filter.technicalId, filter.type, !filter.checked)
              "
            >{{ filter.filterName }}</b-form-checkbox>
            <VueSlider
              v-else-if="filter.type === 'slider'"
              v-model="filter.selectedRange"
              :min="filter.min"
              :max="filter.max"
              :tooltip="'always'"
              @drag-end="
                addFilter(filter.technicalId, filter.type, filter.selectedRange)
              "
            ></VueSlider>
            <input
              v-else-if="filter.type === 'text'"
              v-model="filter.value"
              @keyup.enter="
                addFilter(filter.technicalId, filter.type, filter.value)
              "
            >
          </div>
        </div>
      </b-collapse>
    </div>
  </div>
</template>

<script>
import VueSlider from "vue-slider-component";
import "vue-slider-component/theme/default.css";
import Store from "@/store";

export default {
  components: {
    VueSlider
  },
  props: {
    alloys: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      store: Store.state
    };
  },
  methods: {
    getIcon(iconName) {
      return require("@/assets/" + iconName);
    },
    addFilter(property, type, value) {
      Store.addFilter(property, type, value);
    },
    resetFilters() {
      Store.resetFilters();
    }
  }
};
</script>

