<template>
  <div class="alloy-item">
    <div class="alloy-item-content" :class="{ selected: isSelected }">
      <div class="informations">
        <div class="main-informations">
          <p>{{ alloy.brandName }}</p>
        </div>
        <p>{{ alloy.temper }}</p>
        <div class="more-informations">
          <!-- TODO Dfinir les champs  afficher au survol -->
          <p>{{ alloy.isoDesignation }}</p>
          <p>{{ displayForms(alloy) }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    alloy: {
      type: Object,
      required: true
    },
    isSelected: {
      type: Boolean
    }
  },
  methods: {
    selectAlloy(alloy) {
      this.$emit("select-alloy", alloy);
    },
    displayForms: function(alloy) {
      let forms = [];
      if (alloy.rodForm) forms.push("Rod");
      if (alloy.wireForm) forms.push("Wire");
      if (alloy.stripForm) forms.push("Strip");
      return forms.join(", ");
    }
  }
};
</script>

