<template>
  <div id="datavisualisation" class>
    <div class="header-section">
      <div class="tabs-selector-ctn">
        <b-button
          class="tab-selector"
          :class="{ active: tabName === 'datasheet' }"
          variant="outline-*"
          @click="tabName = 'datasheet'"
        >DATA SHEET</b-button>
        <b-button
          class="tab-selector"
          :class="{ active: tabName === 'graphic' }"
          variant="outline-*"
          @click="tabName = 'graphic'"
        >GRAPHIC OVERVIEW</b-button>
      </div>
      <div class="alloys-ctn">
        <AlloyItem
          v-for="alloy in selectedAlloys"
          :key="alloy.isoDesignation"
          :alloy="alloy"
          class="itemAlloy"
          :isSelected="true"
          @click.native="selectAlloy(alloy)"
        />
        <div v-for="n in 5 - selectedAlloys.length" :key="n" class="empty-alloy-item">
          <div class="dashed"></div>
        </div>
      </div>
    </div>
    <div v-if="tabName === 'graphic'" class="graphic-wrapper">
      <Graphic :chart-data="datas" :options="options"/>
      <div>
        <h6 class="my-3 text-uppercase">Couples de proprits</h6>
        <b-form-group>
          <b-form-radio
            v-for="(couple, index) in couplesList"
            :key="index"
            v-model="selected"
            name="some-radios"
            plain
            class="my-radio"
            :class="selected == index ? 'bold' : ''"
            :value="index"
          >{{ couple.prop1.name + " / " + couple.prop2.name }}</b-form-radio>
        </b-form-group>
      </div>
    </div>
    <div v-else-if="tabName === 'datasheet'" class="dataSheet-wrapper">
      <DataSheet :alloys="selectedAlloys"/>
    </div>
  </div>
</template>

<script>
import Graphic from "@/components/DataVisualization/Graphic.vue";
import DataSheet from "@/components/DataVisualization/DataSheet.vue";
import Couples from "@/config/couples.js";
import AlloyItem from "@/components/AlloyItem";

export default {
  name: "DataVisualization",
  components: {
    Graphic,
    DataSheet,
    AlloyItem
  },
  props: {
    selectedAlloys: {
      type: Array,
      default: null
    }
  },
  data: () => {
    return {
      selected: 0,
      couplesList: JSON.parse(JSON.stringify(Couples)),
      tabName: "datasheet"
    };
  },
  computed: {
    datas: function() {
      if (!this.selectedAlloys.length) {
        return {
          datasets: [{ label: "Aucun alliage slectionn", data: [{}] }]
        };
      }
      let tmpDatasets = [];
      let { prop1, prop2 } = this.couplesList[this.selected];
      this.selectedAlloys.forEach(element => {
        const r = Math.floor(Math.random() * 255);
        const g = Math.floor(Math.random() * 255);
        const b = Math.floor(Math.random() * 255);
        tmpDatasets.push({
          label: element.brandName,
          backgroundColor: "rgba(" + r + "," + g + "," + b + ",0.4)",
          data: [
            {
              x: element[prop1.property],
              y: element[prop2.property],
              r: 10
            }
          ]
        });
      });
      return {
        datasets: tmpDatasets
      };
    },
    options: function() {
      if (!this.selectedAlloys.length) {
        return {};
      }
      let { prop1, prop2 } = this.couplesList[this.selected];
      let prop1Datas = [];
      let prop2Datas = [];
      let tmpOptions = {};
      this.selectedAlloys.forEach(element => {
        prop1Datas.push(element[prop1.property]);
        prop2Datas.push(element[prop2.property]);
      });
      tmpOptions = {
        scales: {
          xAxes: [
            {
              ticks: {
                min: Math.min(...prop1Datas) - prop1.interval,
                max: Math.max(...prop1Datas) + prop1.interval
              },
              scaleLabel: {
                display: true,
                labelString: prop1.name
              }
            }
          ],
          yAxes: [
            {
              ticks: {
                min: Math.min(...prop2Datas) - prop2.interval,
                max: Math.max(...prop2Datas) + prop2.interval
              },
              scaleLabel: {
                display: true,
                labelString: prop2.name
              }
            }
          ]
        }
      };
      return tmpOptions;
    }
  },
  methods: {
    selectAlloy(alloy) {
      this.$emit("select-alloy", alloy);
    }
  }
};
</script>

