<template>
  <div class="container-fluid">
    <SubHeader/>
    <div id="alloySelector" class="row">
      <div class="col-sm-5 col-md-4 no-padding">
        <AlloysFilters :alloys="store.alloys"/>
      </div>
      <div class="col-sm-6 col-md-7">
        <div id="alloys-list" class="row" @scroll="handleScroll">
          <AlloyItem
            v-for="alloy in store.alloys"
            :key="alloy.isoDesignation"
            :alloy="alloy"
            :is-selected="isSelected(alloy)"
            class="alloy-item"
            @click.native="addSelectedAlloy(alloy)"
          />
          <p v-if="!store.alloys.length">Aucun rsultat</p>
        </div>
      </div>
      <div class="col-1 arrows-ctn">
        <div id="arrows">
          <img
            id="right-arrow"
            src="@/assets/arrow.png"
            alt
            :class="{ active: activeArrowRight }"
            @click="scroll('right')"
          >
          <br>
          <img
            id="left-arrow"
            src="@/assets/arrow.png"
            alt
            :class="{ active: activeArrowLeft }"
            @click="scroll('left')"
          >
        </div>
      </div>
    </div>
    <DataVisualization :selected-alloys="selectedAlloys" @select-alloy="addSelectedAlloy"/>
  </div>
</template>

<script>
import axios from "axios";
import AlloyItem from "@/components/AlloyItem";
import AlloysFilters from "@/components/AlloysFilters";
import DataVisualization from "@/components/DataVisualization";
import SubHeader from "@/components/SubHeader";
import Store from "@/store";
import $ from "jquery";

export default {
  name: "AlloysComparator",
  components: {
    AlloyItem,
    AlloysFilters,
    DataVisualization,
    SubHeader
  },
  data() {
    return {
      selectedAlloys: [],
      store: Store.state,
      scrollLeft: 0,
      alloysListWidth: 0
    };
  },
  computed: {
    activeArrowLeft() {
      return this.scrollLeft > 0 ? true : false;
    },
    activeArrowRight() {
      return this.alloysListWidth >= 0 ? true : false;
    }
  },
  created() {
    axios.defaults.baseURL = process.env.VUE_APP_ROOT_API;
    axios
      .get("collections/get/alliages?token=" + process.env.VUE_APP_ROOT_API_KEY)
      .then(response => {
        this.store.alloys = response.data.entries;
        this.store.alloysBackup = response.data.entries;
        Store.sortAlloysItems();
      })
      .catch(function(error) {
        console.log(error);
      });
  },
  methods: {
    handleScroll() {
      this.scrollLeft = $("#alloys-list").scrollLeft();
      this.alloysListWidth =
        $("#alloys-list").get(0).scrollWidth -
        $("#alloys-list").scrollLeft() -
        $("#alloys-list").width();
    },
    scroll(direction) {
      var leftPos = $("#alloys-list").scrollLeft();
      let value = 0;
      if (direction === "right") {
        value = leftPos + 200;
      } else {
        value = leftPos - 200;
      }
      $("#alloys-list").animate(
        {
          scrollLeft: value
        },
        "fast"
      );
    },
    isSelected(alloy) {
      return this.selectedAlloys.find(a => a.brandName === alloy.brandName)
        ? true
        : false;
    },
    addSelectedAlloy(alloy) {
      /* On regarde dans le tableau des alliages slectionns
        Pour voir si l'lment est prsent
      */
      let alloyAlreadySelected = this.selectedAlloys.find(
        a => a.brandName === alloy.brandName
      );
      // Si c'est le cas, on le retire des lments slectionns
      if (alloyAlreadySelected) {
        let index = this.selectedAlloys.indexOf(alloyAlreadySelected);
        this.selectedAlloys.splice(index, 1);
      }
      // Sinon on l'ajoute
      else {
        if (this.selectedAlloys.length < 5) {
          this.selectedAlloys.push(alloy);
        }
      }
    }
  }
};
</script>

