  }
  #alloys-list {
    overflow-x: scroll;
    overflow-y: hidden;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: calc(33.33vw + 20px);
    min-height: 500px;
    align-content: flex-start;
    &::-webkit-scrollbar {
      width: 12px;
      height: 20px;
    }
    &::-webkit-scrollbar-track {
      border-width: 4px 0 0;
      background-color: rgba(0, 0, 0, 0.1);
    }
    &::-webkit-scrollbar-thumb {
      padding: 0 0 0 100px;
      background-color: rgba(0, 0, 0, 0.2);
    }

    &::-webkit-scrollbar-track {
      border-width: 6px 0 1px;
    }
  }
}
.arrows-ctn {
  display: flex;
  -webkit-box-shadow: 0px 15px 32px -23px rgba(107, 107, 107, 1);
  -moz-box-shadow: 0px 15px 32px -23px rgba(107, 107, 107, 1);
  box-shadow: 0px 15px 32px -23px rgba(107, 107, 107, 1);
}

#arrows {
  margin: auto;
  #left-arrow,
